import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false

setTimeout(() => {
  window.document.querySelector('body').innerHTML = '<div id="app"></div>'
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount('#app')
}, 500)
